
.form {
    &-input {
        width: 300px;
        font-size: 16px; }
    &-label {
        display: inline-block;
        min-width: 180px;
        vertical-align: top; }
    &-error ul {
        list-style: none;
        padding: 0;
        color: #CC0000; }
    &-button {
        min-width: 200px;
        font-size: 16px; } }

p {
    margin-bottom: 5px;
    &:last-child {
            margin-bottom: 10px; } }

